Template.bearingcalculation.rendered = function () {
	if (typeof($('#tags').val()) !== 'undefined') {
		$('#tags').val($('#tags').val().replace(/;/g, ','));
	}
	$('#tags').tagsinput({
		confirmKeys: [13, 44, 59], //ENTER, comma and semi-colon
	});

	Meteor.typeahead.inject();
};

Template.bearingcalculation.helpers({
	listActionee: function () {
		//var usernames = ["aa", "bb"];
		return this.data.usernames;
	},
	author: function () {
		return Members.collection.findOne({accountId: this.currentProject.addedBy}).profile.nickname;
	},
	Members: function () {
		return Members.collection.find({}).fetch();
	},
	isProjectManagerChecked: function (projectManagerIds, projectManagerId) {
		if (typeof(projectManagerIds) !== 'undefined') {
			if (projectManagerIds.indexOf(projectManagerId) < 0) {
				return false;
			}
			return "checked";
		}
		return false;
	},
	Minutes: function () {
		//filtered in the publication
	  return Minutes.find().fetch();
	},
	settingsMinutes: function () {
    return {
			rowsPerPage: 10,
			showFilter: true,
			showColumnToggles: true,
			showNavigation: 'auto',
      class: 'table table-condensed col-sm-12',
			fields: [
				{
					key: 'minutesName',
					label: TAPi18n.__("Minutes name"),
					fn: function (value) {
						//var val = '<p class="editable" data-type="textarea" data-placeholder="Enter text" data-emptytext="Click to enter text" data-rows="4">' + value + '</p>';
						return new Spacebars.SafeString(value);
					},
				},
				{
					key: 'minutesTitle',
					label: TAPi18n.__("Minutes title"),
					fn: function (value) {
						//var val = '<p class="editable" data-type="textarea" data-placeholder="Enter text" data-emptytext="Click to enter text" data-rows="4">' + value + '</p>';
						return new Spacebars.SafeString(value);
					},
				},
				{
					key: 'actions',
					label: 'Actions',
					fn: function (value, object) {
						var editLink = '<a href="' +
							Router.routes.editMinutes.path({_id: object._id})
							+ '" title="' + TAPi18n.__('Edit') + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-pencil" aria-hidden="true"></span>' +
							'</a> ';
						var duplicateLink = '<a href="#"'
							+ ' class="duplicateMinutes" title="' + TAPi18n.__('Duplicate') + '"'
							+ ' data-minutesid="' +	object._id + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-duplicate" aria-hidden="true"></span>' +
							'</a> ';
						var deleteLink = ' <a href="#" title="' + TAPi18n.__('Delete') + '" class="deleteMinutes" '
							+ 'data-minutesid="' +	object._id + '"'
							+ '>' +
							'<span class="glyphicon glyphicon-trash" aria-hidden="true"></span>' +
							'</a> ';
						var printLink = '<a href="' +
							Router.routes.printMinutes.path({_id: object._id})
							+ '" title="' + TAPi18n.__('Print') + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-print" aria-hidden="true"></span>' +
							'</a> ';
						return new Spacebars.SafeString(editLink + duplicateLink + printLink + deleteLink);
					},
				},
			],
		};
	},
	Tasks: function () {
		//filtered in the publication
	  return Tasks.find().fetch();
	},
	settingsTasks: function () {
    return {
			rowsPerPage: 10,
			showFilter: true,
			showColumnToggles: true,
			showNavigation: 'auto',
      class: 'table table-condensed col-sm-12',
			rowClass: function (item) {
				var today = moment();
				var scheduledEndDate = moment(item.scheduledEndDate, "DD.MM.YYYY");
				//RKCore.log(today.diff(scheduledEndDate, 'days'));
				if (item.realized === "Yes") {
					return 'success';
				}
				if ( today.diff(scheduledEndDate, 'days') === 0) {
					return 'warning';
				}
				if (today.diff(scheduledEndDate, 'days') > 0) {
					return 'danger';
				}
				return '';
			},
			fields: [
				{
					key: 'taskContent',
					label: TAPi18n.__("Task content"),
					fn: function (value) {
						return new Spacebars.SafeString(value);
					},
				},
				{
					key: 'realized',
					label: TAPi18n.__("Realized"),
					fn: function (value) {
						return new Spacebars.SafeString(value);
					},
				},
				{
					key: 'actions',
					label: 'Actions',
					fn: function (value, object) {
						var markAsDoneLink = '';
						var editLink;
						var duplicateLink;
						var deleteLink;
						RKCore.log(object);
						if (object.realized === "No") {
							markAsDoneLink = ' <a href="#" title="' + TAPi18n.__('Mark as done') + '" class="markAsDone" '
								+ 'data-taskid="' +	object._id + '"'
								+ '>' +
								'<span class="glyphicon glyphicon-ok" aria-hidden="true"></span>' +
								'</a> ';
						}
						editLink = '<a href="' +
							Router.routes.editTask.path({_id: object._id})
							+ '" title="' + TAPi18n.__('Edit') + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-pencil" aria-hidden="true"></span>' +
							'</a> ';
						duplicateLink = '<a href="#"'
							+ ' class="duplicateTask" title="' + TAPi18n.__('Duplicate') + '"'
							+ ' data-taskid="' +	object._id + '"'
							+ '>'
							+ '<span class="glyphicon glyphicon-duplicate" aria-hidden="true"></span>' +
							'</a> ';
						deleteLink = ' <a href="#" title="' + TAPi18n.__('Delete') + '" class="deleteTask" '
							+ 'data-taskid="' +	object._id + '"'
							+ '>' +
							'<span class="glyphicon glyphicon-trash" aria-hidden="true"></span>' +
							'</a> ';
						return new Spacebars.SafeString(markAsDoneLink + editLink + duplicateLink + deleteLink);
					},
				},
			],
		};
	},
});

Template.bearingcalculation.events({
	'submit form': function (e) {
		var data = {};
		var projectManagerIds = [];
	  e.preventDefault();
		data.projectId = this.currentProject._id;
		data.projectNumber = e.target.projectNumber.value;
		data.projectName = e.target.projectName.value;
		data.projectDescription = e.target.projectDescription.value;
		data.tags = e.target.tags.value;
		data.tagsNoHTML = $("<div/>").html(e.target.tags.value).text();

		$("input:checkbox.projectManagerIds[type=checkbox]:checked").each(function () {
			projectManagerIds.push($(this).val());
		});
		data.projectManagerIds = projectManagerIds;

	  Meteor.call('updateProject', data, function (error) {
			if (!error) {
				if (typeof(toastr) !== 'undefined') {
					toastr.success(TAPi18n.__('The project has been updated successfully'));
				}
				Router.go("viewProjects");
			}
		});
		return false;
	},
	"click a.duplicateMinutes": function (e) {
    e.preventDefault();
    Meteor.call('duplicateMinutes', e.currentTarget.dataset.minutesid, function (error, result) {
			if (!error) {
				if (typeof(toastr) !== 'undefined') {
					toastr.success(TAPi18n.__('The minutes has been duplicated successfully. Please edit and save.'));
				}
				newId = result;
				Router.go("editMinutes", {_id: newId});
			}
		});
		return false;
	},
	"click a.deleteMinutes": function (e) {
    e.preventDefault();
		bootbox.confirm(TAPi18n.__("Are you sure you want to delete this minutes ?"), function (result) {
		 if (result) {
			 Meteor.call('deleteMinutes', e.currentTarget.dataset.minutesid);
		 }
		});
		return false;
	},
	"click a.duplicateTask": function (e) {
    e.preventDefault();
    Meteor.call('duplicateTask', e.currentTarget.dataset.taskid, function (error, result) {
			if (!error) {
				if (typeof(toastr) !== 'undefined') {
					toastr.success(TAPi18n.__('The task has been duplicated successfully. Please edit and save.'));
				}
				newId = result;
				Router.go("editTask", {_id: newId});
			}
		});
		return false;
	},
	"click a.deleteTask": function (e) {
    e.preventDefault();
		bootbox.confirm(TAPi18n.__("Are you sure you want to delete this task ?"), function (result) {
		 if (result) {
			 Meteor.call('deleteTask', e.currentTarget.dataset.taskid);
		 }
		});
		return false;
	},
	"click a.markAsDone": function (e) {
	    e.preventDefault();
	    Meteor.call('markAsDone', e.currentTarget.dataset.taskid);
			return false;
	},
});
